'use client';

import { useEffect, useMemo, useState } from 'react';
import { useAccount, usePublicClient } from 'wagmi';
import { ConnectButton } from '@rainbow-me/rainbowkit';
import { sdk } from '@farcaster/miniapp-sdk';
import { CONTRACT_ADDRESS } from '@/lib/constants';
import { ipfsToHttp } from '@/lib/ipfs';

// حدث Transfer قياسي مختصر بدون ملف منفصل
const TransferEvent = {
  type: 'event',
  name: 'Transfer',
  inputs: [
    { indexed: true, name: 'from', type: 'address' },
    { indexed: true, name: 'to', type: 'address' },
    { indexed: true, name: 'tokenId', type: 'uint256' },
  ],
} as const;

// baseURI من المعطيات التي زوّدتنا بها
const BASE_URI = 'ipfs://bafybeif4nhbmbypovzvr2wz5p2kfm2wy2simfbbxawmkk2j3cjp4guxsgy/';

type Meta = Record<string, any>;

export default function MyNftsPage() {
  const { address, isConnected } = useAccount();
  const publicClient = usePublicClient();

  // Farcaster ready()
  useEffect(() => {
    if (typeof window === 'undefined') return;
    const appUrl = process.env.NEXT_PUBLIC_APP_URL;
    const sameOrigin = !appUrl || window.location.origin === appUrl;
    if (sameOrigin) sdk.actions.ready().catch(console.error);
  }, []);

  const [tokenId, setTokenId] = useState<bigint | null>(null);
  const [imageUrl, setImageUrl] = useState<string | null>(null);
  const [metadataUrl, setMetadataUrl] = useState<string | null>(null);
  const [metadata, setMetadata] = useState<Meta | null>(null);
  const [loading, setLoading] = useState(false);
  const [err, setErr] = useState<string | null>(null);

  // اجلب آخر NFT سكّيته (أحدث Transfer إلى محفظتك)
  useEffect(() => {
    let cancelled = false;
    async function run() {
      if (!isConnected || !address || !publicClient) return;
      setLoading(true);
      setErr(null);
      setTokenId(null);
      setImageUrl(null);
      setMetadata(null);
      try {
        const logs = await publicClient.getLogs({
          address: CONTRACT_ADDRESS,
          // @ts-ignore
          event: TransferEvent,
          // حصر اللوجات إلى عنوانك كمستلم
          // @ts-ignore
          args: { to: address },
        });

        if (!logs.length) {
          if (!cancelled) setErr('لا توجد عمليات Mint (Transfer) إلى محفظتك حتى الآن.');
          return;
        }

        // اختر الأحدث بناءً على (blockNumber, logIndex)
        const latest = logs.slice().sort((a: any, b: any) => {
          if (a.blockNumber === b.blockNumber) return Number(a.logIndex - b.logIndex);
          return Number(a.blockNumber - b.blockNumber);
        })[logs.length - 1] as any;

        const tid: bigint = latest.args.tokenId;
        const metaUrl = ipfsToHttp(`${BASE_URI}${tid}.json`);

        // اجلب الميتاداتا
        const res = await fetch(metaUrl, { cache: 'no-store' });
        if (!res.ok) throw new Error(`Metadata HTTP ${res.status}`);
        const json = (await res.json()) as Meta;

        const img = ipfsToHttp(
          (json.image as string) ||
          (json.image_url as string) ||
          ''
        );

        if (!cancelled) {
          setTokenId(tid);
          setMetadataUrl(metaUrl);
          setMetadata(json);
          setImageUrl(img || null);
        }
      } catch (e: any) {
        if (!cancelled) setErr(e?.message || String(e));
      } finally {
        if (!cancelled) setLoading(false);
      }
    }
    run();
    return () => { cancelled = true; };
  }, [isConnected, address, publicClient]);

  const prettyMeta = useMemo(() => {
    if (!metadata) return '';
    try {
      return JSON.stringify(metadata, null, 2);
    } catch {
      return String(metadata);
    }
  }, [metadata]);

  return (
    <main className="min-h-screen bg-neutral-950 text-white">
      <header className="flex items-center justify-between p-4 border-b border-neutral-800">
        <div className="text-lg font-semibold">RACCOON-MINT — My NFT</div>
        <ConnectButton />
      </header>

      <section className="max-w-xl mx-auto p-6 grid gap-6">
        {!isConnected ? (
          <p className="text-sm text-neutral-300">
            Connect your wallet to view your latest minted NFT.
          </p>
        ) : loading ? (
          <p className="text-sm text-neutral-300">Loading your latest minted NFT…</p>
        ) : err ? (
          <p className="text-sm text-amber-300">⚠️ {err}</p>
        ) : tokenId === null ? (
          <p className="text-sm text-neutral-300">No NFT found.</p>
        ) : (
          <>
            {/* الصورة فقط */}
            <article className="rounded-2xl overflow-hidden border border-neutral-800 bg-neutral-900">
              <div className="aspect-square bg-neutral-800 flex items-center justify-center">
                {imageUrl ? (
                  // eslint-disable-next-line @next/next/no-img-element
                  <img
                    src={imageUrl}
                    alt={metadata?.name ?? `#${tokenId.toString()}`}
                    className="w-full h-full object-cover"
                  />
                ) : (
                  <span className="text-xs text-neutral-400 p-2 text-center">
                    No image in metadata
                  </span>
                )}
              </div>
              <div className="p-4 text-sm space-y-1">
                <div className="font-mono">Token #{tokenId.toString()}</div>
                {metadataUrl && (
                  <a
                    className="text-cyan-300 hover:underline"
                    href={metadataUrl}
                    target="_blank"
                    rel="noreferrer"
                  >
                    Open metadata JSON →
                  </a>
                )}
              </div>
            </article>

            {/* الميتاداتا أسفل الصورة */}
            <div className="rounded-2xl border border-neutral-800 bg-neutral-900/60 p-4">
              <h3 className="font-semibold mb-2">Metadata</h3>
              <pre className="text-xs text-neutral-200 overflow-auto whitespace-pre-wrap">
{prettyMeta}
              </pre>
            </div>
          </>
        )}
      </section>
    </main>
  );
}
